@import "../styles/variables.scss";

.switch {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: transparent;
	color: $colorLight;
	padding: 5px 15px;
	border: 1px solid $colorLight;
	border-radius: 10px;
}

.switchBody {
	width: 40px;
	height: 20px;
	border: 1px solid $colorLight;
	border-radius: 25px;
	transition: $transitionTime;
	cursor: pointer;

	&::after {
		content: "";
		display: block;
		width: 19px;
		height: 19px;
		border: 1px solid $colorLight;
		border-radius: 100%;
		background-color: $colorLight;
		transition: $transitionTime;
	}

	&:hover {
		outline: 1px dotted $colorGray;
	}
}

.input {
	display: none;
}

.switchChecked .switchBody::after {
	transform: translateX(101%);
}
