@import "../styles/variables.scss";

.nav {
	width: auto;
	margin: 0 auto;
}

.list {
	display: flex;
}

.item {
	width: 50%;
}

.btn {
	width: 100%;
	padding: 5px 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: transparent;
	border: 1px solid $colorLight;
	border-bottom: none;
	color: $colorLight;
	cursor: pointer;
	transition: $transitionTime;

	&:hover {
		outline: 1px dotted $colorGray;
	}
}

.itemActive .btn {
	background-color: $colorLight;
	border-color: $colorLight;
	color: $colorDark;
}

@media only screen and (min-width: 500px) {
	.nav {
		max-width: 300px;
		margin: 0 auto;
	}

	.list .item:first-child .btn {
		border-top-left-radius: 10px;
	}

	.list .item:last-child .btn {
		border-top-right-radius: 10px;
	}
}
