@import url('https://fonts.googleapis.com/css?family=Muli:300&display=swap');

*,
*::after,
*::before {
	padding: 0;
	margin: 0;
	box-sizing: inherit;
}

*:focus {
	outline: 1px dotted $colorGray;
}

body {
	box-sizing: border-box;
	font-family: "Muli", sans-serif;
	font-weight: 300;
	font-size: 18px;
	line-height: 1.5;
	background: linear-gradient(-45deg, lighten($colorBg, 10%), $colorBg);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	color: $colorLight;
}

button {
	font-family: inherit;
	font-size: inherit;
	color: inherit;
}

ul {
	list-style: none;
}
