@import "../styles/variables";

.btn {
	width: $btnSize;
	height: $btnSize;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 1px solid $colorLight;
	border-radius: 100%;
	background-color: transparent;
	color: $colorLight;
	transition: $transitionTime;
	cursor: pointer;

	&:hover {
		outline: 1px dotted $colorGray;
	}
}
