.container {
	position: relative;
}

.main > * {
	min-height: 100vh;
}

.footer {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
}

.timer,
.settings {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.timer > *:first-child {
	margin-left: 250px;
}

.settings > * {
	width: 90%;
	max-width: 300px;

	&:not(:last-child) {
		margin-bottom: 20px;
	}
}
