@import "../styles/variables.scss";

.range {
	background-color: transparent;
	color: $colorLight;
	padding: 5px 15px;
	border: 1px solid $colorLight;
	border-radius: 10px;

	& > * {
		background-color: transparent;
	}
}

.label {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.input {
	width: 100%;
	cursor: pointer;

	&:hover {
		outline: 1px dotted $colorGray;
	}
}
